			<!-- ÂçïÂè™ÂÆ†Áâ©ÔºöÊ≤øÁî®ÂéüÊ†∑Âºè -->
			<view v-if="hasPet && pets.length === 1" class="pet-content" @tap="goPetDetail(currentPet)">
				<view class="pet-left">
					<view class="pet-avatar">
						<image 
