					<view class="pet-tags" v-if="petTags.length">
						<text v-for="(tag, i) in petTags" :key="i" class="tag">{{ tag }}</text>
					</view>
				</view>
			</view>

			<!-- Â§öÂè™ÂÆ†Áâ©ÔºöÊ®™ÂêëÊªëÂä® -->
			<scroll-view v-else-if="hasPet && pets.length > 1" class="pet-scroll" scroll-x="true" show-scrollbar="false">
				<view class="pet-item" v-for="pet in pets" :key="pet.id" @tap="goPetDetail(pet)">
					<view class="pet-container">
						<view class="pet-left">
								<view class="pet-avatar">
									<image 
										v-if="pet?.avatarUrl" 
										:src="pet.avatarUrl" 
										class="pet-avatar-inner" 
										mode="aspectFill" 
									/>
									<image 
										v-else 
										:src="getDefaultPetAvatar()" 
										class="pet-avatar-inner" 
										mode="aspectFill" 
									/>
								</view>
							</view>
							<view class="pet-right">
								<view class="pet-title-row">
									<text class="pet-name">{{ pet?.name || 'ÊàëÁöÑÂÆ†Áâ©' }}</text>
									<text class="pet-edit">‚úé</text>
								</view>
								<text class="pet-meta">{{ getPetMeta(pet) }}</text>
								<view class="pet-tags" v-if="getPetTags(pet).length">
									<text v-for="(tag, i) in getPetTags(pet)" :key="i" class="tag">{{ tag }}</text>
								</view>
							</view>
						
					</view>
				</view>
			</scroll-view>
			<view v-else class="pet-empty" @tap="goAddPet">
				<image class="add-icon" src="/static/index/add.png" mode="widthFix" />
				<text class="add-text">Ê∑ªÂä†ÊàëÁöÑÂÆ†Áâ©</text>
			</view>
		</view>

		<!-- Quick list + small cards -->
		<view class="row">
			<view class="todo-card">
				<scroll-view class="todo-scroll" scroll-y="true">
					<view v-if="homeReminders.length === 0" class="todo-item">
						<view class="bullet"></view><text>‰ªäÂ§©ÊöÇÊó†ÊèêÈÜí</text>
					</view>
					<view v-else>
						<view class="todo-item" v-for="r in homeReminders" :key="r.id">
							<view class="bullet"></view>
							<text>{{ r.title }}</text>
						</view>
					</view>
				</scroll-view>
			</view>
			<view class="side-cards">
				<view class="side-card" @tap="goToRecord('calendar')">
					<view class="side-texts">
						<text class="side-title">ËÆ∞ÂΩï</text>
						<text class="side-sub">Êõ¥Â•ΩÁöÑÁÖßÈ°æÂ∞èÂÆ†</text>
					</view>
					<image class="side-icon" src="/static/index/record.png" mode="widthFix" />
				</view>
				<view class="side-card" @tap="goToRecord('stats')">
					<view class="side-texts">
						<text class="side-title">ÊèêÈÜí</text>
						<text class="side-sub">Èò≤Ê≠¢ÂøòËÆ∞</text>
					</view>
					<image class="side-icon" src="/static/index/note.png" mode="widthFix" />
				</view>
			</view>
		</view>

		<!-- Popular science -->
		<view class="science" @tap="goScienceDetail">
			<view class="science-hd">
				<view class="science-title">
					<text>‰ªäÊó•ÁßëÊôÆ</text>
					<view class="title-notch"></view>
				</view>
				<text class="science-sub">{{ dailyScience?.title ? `„Äê${dailyScience.title}„Äë` : '„Äê‰ªäÊó•Â∞èÁü•ËØÜ„Äë' }}</text>
			</view>
			<view class="science-text">{{ dailyScience?.content || 'ÊØèÊó•‰∏∫‰Ω†Êé®Ëçê‰∏ÄÊù°ÂÆ†Áâ©ÂÅ•Â∫∑Â∞èÁü•ËØÜÔΩû' }}</view>
			<image class="science-illust" src="/static/index/popular-science.png" mode="widthFix" />
			<view class="science-icons">
				<image class="fish" src="/static/index/fish.png" mode="widthFix" />
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { onShow } from '@dcloudio/uni-app'
import { initState, getState } from '@/utils/store.js'
import { api } from '@/utils/api.js'

defineOptions({ name: 'HomeIndex' })

const hasPet = ref(false)
const userInfo = ref(null)
const pets = ref([])
const homeReminders = ref([])
const dailyScience = ref(null)
const currentPet = computed(() => {
  const pet = pets.value?.[0] || null
  console.log('currentPet computed:', pet)
  return pet
})
const petMeta = computed(() => {
  if (!currentPet.value) return ''
  const months = currentPet.value.months ? `${currentPet.value.months}‰∏™Êúà` : ''
  const weight = currentPet.value.weight ? `${currentPet.value.weight}kg` : ''
  return [months, weight].filter(Boolean).join(' | ')
})
const petTags = computed(() => {
  if (!currentPet.value) return []
  const temperament = currentPet.value.temperament || ''
  // temperament ÂèØËÉΩÊòØ‰ª•ÈÄóÂè∑ÂàÜÈöîÁöÑÂ≠óÁ¨¶‰∏≤ÔºåÊàñ‰∏Ä‰∏™ÁÆÄÂçïÂ≠óÁ¨¶‰∏≤
  if (!temperament) return []
  if (Array.isArray(temperament)) return temperament
  return String(temperament).split(/[Ôºå,\s]+/).filter(Boolean).slice(0, 3)
})

// Â§öÂÆ†Áâ©Âç°ÁâáÁöÑ meta ‰∏é tags ËÆ°ÁÆó
function getPetMeta(pet) {
  if (!pet) return ''
  const months = pet.months ? `${pet.months}‰∏™Êúà` : ''
  const weight = pet.weight ? `${pet.weight}kg` : ''
  return [months, weight].filter(Boolean).join(' | ')
}

function getPetTags(pet) {
  if (!pet) return []
  const temperament = pet.temperament || ''
  if (!temperament) return []
  if (Array.isArray(temperament)) return temperament.slice(0, 3)
  return String(temperament).split(/[Ôºå,\s]+/).filter(Boolean).slice(0, 3)
}

// ÂàùÂßãÂåñÈ°µÈù¢
onMounted(async () => {
  // ÂàùÂßãÂåñÁä∂ÊÄÅ
  const state = initState()
  userInfo.value = state.userInfo
  
  // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
  if (!state.isLoggedIn) {
    uni.reLaunch({
      url: '/pages/login/login'
    })
    return
  }
  
  // Âä†ËΩΩÂÆ†Áâ©Êï∞ÊçÆ
  await loadPets()
  await loadTodayReminders()
  await loadDailyScience()
})

// ËøîÂõûÂà∞È¶ñÈ°µÊó∂Ëá™Âä®Âà∑Êñ∞ÂÆ†Áâ©Êï∞ÊçÆ
onShow(async () => {
  // Â¶ÇÊûú‰ªéËØ¶ÊÉÖËøîÂõû‰∏îÊî∂Âà∞ÂÖ®Â±Ä‰∫ã‰ª∂ÔºåÊàñÁõ¥Êé•ËøîÂõûÈ°µÈù¢ÔºåÈÉΩÂà∑Êñ∞‰∏ÄÊ¨°
  await loadPets()
  await loadTodayReminders()
  await loadDailyScience()
})

// Â§ÑÁêÜÂõæÁâáURLÔºåÁ°Æ‰øùÂèØ‰ª•Ê≠£Â∏∏ËÆøÈóÆ
function processImageUrl(url) {
  if (!url) return null
  
  // Â¶ÇÊûúÊòØwxfileÂçèËÆÆÔºàÂ∞èÁ®ãÂ∫è‰∏¥Êó∂Êñá‰ª∂ÔºâÔºåÁõ¥Êé•ËøîÂõû
  if (url.startsWith('wxfile://')) {
    return url
  }
  
  // Â¶ÇÊûúÊòØÂÆåÊï¥ÁöÑHTTP/HTTPS URLÔºåÁõ¥Êé•ËøîÂõû
  if (url.startsWith('http://') || url.startsWith('https://')) {
    return url
  }
  
  // Â¶ÇÊûúÊòØÁõ∏ÂØπË∑ØÂæÑÔºåÊ∑ªÂä†Âü∫Á°ÄURL
  if (url.startsWith('/')) {
    return `http://pet-api.zbinli.cn${url}`
  }
  
  // Â¶ÇÊûú‰∏çÊòØ‰ª•/ÂºÄÂ§¥ÔºåÊ∑ªÂä†Âü∫Á°ÄURLÂíå/
  return `http://pet-api.zbinli.cn/${url}`
}

// Ëé∑ÂèñÈªòËÆ§ÂÆ†Áâ©Â§¥ÂÉè
function getDefaultPetAvatar() {
  return '/static/index/add.png' // ‰ΩøÁî®Áé∞ÊúâÁöÑÊ∑ªÂä†ÂõæÊ†á‰Ωú‰∏∫ÈªòËÆ§Â§¥ÂÉè
}

// Âä†ËΩΩÂÆ†Áâ©Êï∞ÊçÆ
async function loadPets() {
  try {
    console.log('=== È¶ñÈ°µÂä†ËΩΩÂÆ†Áâ©Êï∞ÊçÆË∞ÉËØï‰ø°ÊÅØ ===');
    const result = await api.getPets()
    console.log('APIËøîÂõûÁªìÊûú:', result);
    
    // ÂÖºÂÆπÂêéÁ´ØÁõ¥Êé•ËøîÂõûÊï∞ÁªÑ Êàñ ÂåÖÂú® data Èáå
    pets.value = Array.isArray(result) ? result : (result.data || [])
    
    // Â§ÑÁêÜÂõæÁâáURL
    pets.value = pets.value.map(pet => ({
      ...pet,
      avatarUrl: processImageUrl(pet.avatarUrl)
    }))
    
    console.log('Â§ÑÁêÜÂêéÁöÑÂÆ†Áâ©Êï∞ÊçÆ:', pets.value);
    console.log('Á¨¨‰∏Ä‰∏™ÂÆ†Áâ©ÁöÑÂ§¥ÂÉèURL:', pets.value[0]?.avatarUrl);
    console.log('Á¨¨‰∏Ä‰∏™ÂÆ†Áâ©ÁöÑÂÆåÊï¥Êï∞ÊçÆ:', pets.value[0]);
    
    hasPet.value = pets.value.length > 0
    console.log('ÊòØÂê¶ÊúâÂÆ†Áâ©:', hasPet.value);
    
    // Ê£ÄÊü•ÂõæÁâáURLÊòØÂê¶ÊúâÊïà
    if (pets.value[0]?.avatarUrl) {
      console.log('Â§¥ÂÉèURLËØ¶ÊÉÖ:', {
        url: pets.value[0].avatarUrl,
        type: typeof pets.value[0].avatarUrl,
        length: pets.value[0].avatarUrl.length
      });
    }
  } catch (error) {
    console.error('Âä†ËΩΩÂÆ†Áâ©Êï∞ÊçÆÂ§±Ë¥•:', error)
  }
}

// Âä†ËΩΩ‰ªäÊó•ÊèêÈÜí
async function loadTodayReminders() {
  try {
    const now = new Date()
    const start = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 0, 0, 0, 0).toISOString()
    const end = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 23, 59, 59, 999).toISOString()
    const res = await api.getSubscriptions({ startDate: start, endDate: end })
    const list = Array.isArray(res) ? res : (res.subscriptions || res.data || [])
    homeReminders.value = list.map(s => ({
      id: s.id,
      title: s.content || (s.type === 'medicine' ? 'Áî®ËçØÊèêÈÜí' : (s.type === 'vaccine' ? 'Áñ´ËãóÊèêÈÜí' : (s.type === 'wash' ? 'Ê¥óÊä§ÊèêÈÜí' : 'Êó•Â∏∏ÊèêÈÜí'))),
      time: (new Date(s.fireAt)).toTimeString().slice(0,5)
    }))
  } catch (e) {
    homeReminders.value = []
  }
}

// Âä†ËΩΩ"‰ªäÊó•ÁßëÊôÆ"ÔºöÂõ∫ÂÆöËé∑ÂèñÁ¨¨‰∏ÄÁØáarticleÊñáÁ´†
async function loadDailyScience() {
  try {
    console.log('üîç ÂºÄÂßãÂä†ËΩΩ‰ªäÊó•ÁßëÊôÆ...')
    const res = await api.getArticles({ page: 1, limit: 1 })
    console.log('üì° ÁßëÊôÆAPIËøîÂõû:', res)
    
    const list = Array.isArray(res) ? res : (res.articles || res.data || [])
    console.log('üìã ÁßëÊôÆÊñáÁ´†ÂàóË°®:', list)
    
    if (!list.length) {
      console.log('‚ö†Ô∏è Ê≤°ÊúâÊâæÂà∞ÁßëÊôÆÊñáÁ´†')
      dailyScience.value = null
      return
    }
    
    // Âõ∫ÂÆöËé∑ÂèñÁ¨¨‰∏ÄÁØáÊñáÁ´†
    const selectedArticle = list[0]
    console.log('‚úÖ ÈÄâ‰∏≠ÁöÑÁßëÊôÆÊñáÁ´†ÔºàÁ¨¨‰∏ÄÁØáÔºâ:', selectedArticle)
    
    // Â§ÑÁêÜÂÜÖÂÆπÊà™Êñ≠ÂíånullÂÄº
    if (selectedArticle) {
      if (!selectedArticle.content || selectedArticle.content === null) {
        // Â¶ÇÊûúcontent‰∏∫nullÔºå‰ΩøÁî®title‰Ωú‰∏∫ÂÜÖÂÆπ
        selectedArticle.content = selectedArticle.title || 'ÊöÇÊó†ÂÜÖÂÆπ'
        console.log('‚ö†Ô∏è ÊñáÁ´†content‰∏∫nullÔºå‰ΩøÁî®title‰Ωú‰∏∫ÂÜÖÂÆπ:', selectedArticle.content)
      } else {
        const maxLength = 120 // ÊúÄÂ§ßÊòæÁ§∫Â≠óÁ¨¶Êï∞
        const content = selectedArticle.content
        if (content.length > maxLength) {
          selectedArticle.content = content.substring(0, maxLength) + '...'
          console.log('‚úÇÔ∏è ÂÜÖÂÆπÂ∑≤Êà™Êñ≠:', selectedArticle.content)
        }
      }
    }
    
    dailyScience.value = selectedArticle
    console.log('üéØ ÊúÄÁªàÁßëÊôÆÊï∞ÊçÆ:', dailyScience.value)
  } catch (e) {
    console.error('‚ùå Âä†ËΩΩ‰ªäÊó•ÁßëÊôÆÂ§±Ë¥•:', e)
    dailyScience.value = null
  }
}

// Ëé∑ÂèñÈóÆÂÄôËØ≠
function getGreeting() {
  const hour = new Date().getHours()
  if (hour < 12) {
    return 'Good Morning!'
  } else if (hour < 18) {
    return 'Good Afternoon!'
  } else {
    return 'Good Evening!'
  }
}

function goAddPet() {
	uni.navigateTo({ url: '/pages/createPet/createPet' })
}

function goToRecord(tab) {
	// Â∞Ü tab ÂèÇÊï∞Â≠òÂÇ®Âà∞ÂÖ®Â±ÄÁä∂ÊÄÅ
	uni.setStorageSync('recordTab', tab)
	// Ë∑≥ËΩ¨Âà∞ tabBar È°µÈù¢
	uni.switchTab({ url: '/pages/record/record' })
}

// ÂõæÁâáÂä†ËΩΩ‰∫ã‰ª∂Â§ÑÁêÜ
function onImageLoad(e) {
  console.log('ÂõæÁâáÂä†ËΩΩÊàêÂäü:', e)
}

function onImageError(e) {
  console.error('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', e)
  console.error('Â§±Ë¥•ÁöÑÂõæÁâáURL:', currentPet.value?.avatarUrl)
  
  // ÂΩìÂõæÁâáÂä†ËΩΩÂ§±Ë¥•Êó∂Ôºå‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè
  if (currentPet.value) {
    currentPet.value.avatarUrl = getDefaultPetAvatar()
  }
}

function goPetDetail(pet) {
  pet = pet || currentPet.value
  console.log('=== È¶ñÈ°µË∑≥ËΩ¨ÂÆ†Áâ©ËØ¶ÊÉÖË∞ÉËØï‰ø°ÊÅØ ===');
  console.log('ÂΩìÂâçÂÆ†Áâ©Êï∞ÊçÆ:', pet);
  console.log('ÂÆ†Áâ©Â§¥ÂÉèURL:', pet?.avatarUrl);
  
  if (!pet || !pet.id) return
  const q = encodeURIComponent(JSON.stringify(pet))
  console.log('ÁºñÁ†ÅÂêéÁöÑÊï∞ÊçÆ:', q);
  console.log('Ë∑≥ËΩ¨URL:', `/pages/petDetail/petDetail?pet=${q}`);
  
  uni.navigateTo({ url: `/pages/petDetail/petDetail?pet=${q}` })
}

// Ë∑≥ËΩ¨Âà∞ÁßëÊôÆËØ¶ÊÉÖÈ°µ
function goScienceDetail() {
  if (!dailyScience.value || !dailyScience.value.id) {
    console.log('‚ö†Ô∏è Ê≤°ÊúâÁßëÊôÆÂÜÖÂÆπÂèØË∑≥ËΩ¨')
    return
  }
  
  console.log('üîç Ë∑≥ËΩ¨ÁßëÊôÆËØ¶ÊÉÖ:', dailyScience.value)
  
  // Ë∑≥ËΩ¨Âà∞ÁßëÊôÆËØ¶ÊÉÖÈ°µ
  uni.navigateTo({
    url: `/pages/scienceDetail/scienceDetail?id=${dailyScience.value.id}`,
    success: (res) => {
      console.log('‚úÖ ÁßëÊôÆËØ¶ÊÉÖÈ°µË∑≥ËΩ¨ÊàêÂäü')
      try {
        res.eventChannel.emit('science', dailyScience.value)
        console.log('üì§ Â∑≤ÂèëÈÄÅÁßëÊôÆÊï∞ÊçÆÂà∞ËØ¶ÊÉÖÈ°µ:', dailyScience.value)
      } catch (e) {
        console.error('‚ùå ÂèëÈÄÅÁßëÊôÆÊï∞ÊçÆÂ§±Ë¥•:', e)
      }
    },
    fail: (err) => {
      console.error('‚ùå ÁßëÊôÆËØ¶ÊÉÖÈ°µË∑≥ËΩ¨Â§±Ë¥•:', err)
    }
  })
}
</script>

<style>
